}

:root {
  --font-family-primary: "Roboto", sans-serif;
  --bg-color-header-fallback: rgba(0, 0, 0, .44);
  --bg-color-backdrop: rgba(0, 0, 0, .6);
  --border-color-card: #eee;
  --color-black: #000;
  --color-white: #fff;
  --color-grey: #545454;
  --color-light-grey: #f7f7f7;
  --color-accent-primary: #ff001b;
  --color-accent-secondary: #ff6b08;
  --bg-color-theme-light: #eee;
  --bg-color-theme-dark: #4a4a4a;
  --bg-color-theme-accent-dark: radial-gradient( circle at 100%, #1f1f1f 35%, #ff6b08 50%, #ff001b 60%, #1f1f1f 75%);
  --bg-color-theme-accent-dark-btn: linear-gradient( 90deg, #ff001b 0%, #1f1f1f 50%, #ff6b08 100%);
  --font-color-grey: #8c8c8c;
  --duration: 250ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --timing-function-form: cubic-bezier(.68, -.55, .265, 1.55);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
