    justify-content: left;
    gap: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__list {
    justify-content: left;
    column-gap: 16px;
  }
}

.gallery__item {
  width: 280px;
  min-height: 445px;
  background-color: var(--color-white);
  cursor: pointer;
  transition: transform var(--duration) var(--timing-function), box-shadow var(--duration) var(--timing-function);
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow: hidden;
  transform: scale(1);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.gallery__item:hover, .gallery__item:focus {
  box-shadow: none;
  transform: scale(.98);
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .gallery__item {
    width: 336px;
    min-height: 494px;
  }
}

@media screen and (min-width: 1280px) {
