  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__box {
  letter-spacing: 0;
  color: var(--color-grey);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  display: flex;
}

