    margin-right: 3px;
  }
}

.footer__team {
  display: flex;
}

.footer__team-text:not(:last-child) {
  margin-right: 10px;
}

.footer__team-link {
  cursor: pointer;
  transition: color var(--duration) var(--timing-function);
  text-decoration: underline;
}

.footer__team-link:hover, .footer__team-link:focus {
  color: var(--color-accent-secondary);
}

.heartbeat {
  color: var(--color-accent-secondary);
  transform-origin: center;
  cursor: pointer;
  animation: heartbeat 3s infinite;
}

.heartbeat:hover, .heartbeat:focus {
  animation-play-state: paused;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }

  20% {
    transform: scale(1.5);
  }

  40% {
    transform: scale(1.3);
  }

  60% {
    transform: scale(1.5);
  }

  80% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

.menu-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu-nav__list {
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu-nav__item:first-child {
  margin-right: 40px;
}

.menu-nav__link {
  color: var(--color-white);
  text-transform: uppercase;
  transition: color var(--duration) var(--timing-function);
  padding-top: 10px;
  padding-bottom: 3px;
  position: relative;
}

.menu-nav__link:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--color-accent-primary);
  transition: transform var(--duration) var(--timing-function);
  display: block;
  position: absolute;
  bottom: 0;
  transform: scaleX(0);
}

.menu-nav__link.current:after, .menu-nav__link:hover:after, .menu-nav__link:focus:after {
  transform: scaleX(1);
}

