  }
}

.search-form__input {
  width: 100%;
  letter-spacing: 0;
  color: var(--color-white);
  border: none;
  border-bottom: 1px solid var(--color-white);
  transition: border var(--duration) var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  outline: none;
  padding: 0 2px 3px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
