fieldset {
  border: none;
  padding: 0;
}

main {
  min-height: 260px;
  transition: background-color 2000ms var(--timing-function);
}

main .gallery__queue-list > li > p, main .gallery__watched-list > li > p {
  min-width: 260px;
  text-align: center;
  letter-spacing: .08em;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.38;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

main .gallery__queue-list > li > p a, main .gallery__watched-list > li > p a {
  color: var(--color-accent-primary);
  text-decoration: underline;
}

main[data-theme="light"] {
  background-color: var(--bg-color-theme-light);
}

main[data-theme="dark"] {
  background-color: var(--bg-color-theme-dark);
}

main[data-theme="dark"] .gallery__queue-list > li > p, main[data-theme="dark"] .gallery__watched-list > li > p {
