}

.c-toggle {
  text-align: center;
}

.theme-switcher {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.theme-switcher .input-wrapper {
  width: 35px;
  height: 3px;
  border-radius: 3px;
  margin: 10px 5px;
  display: inline-block;
  position: relative;
}

.theme-switcher label {
  width: 100%;
  height: 100%;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 3px;
  display: block;
}

.theme-switcher label[data-color="light"] {
  background-color: var(--bg-color-theme-light);
}

.theme-switcher label[data-color="dark"] {
  background-color: var(--bg-color-theme-dark);
}

.theme-switcher label[data-color="accent-dark"] {
  background: var(--bg-color-theme-accent-dark-btn);
}

.theme-switcher input[type="radio"] {
  width: 100%;
  height: 20px;
  opacity: 0;
  cursor: pointer;
  z-index: 1000;
  position: absolute;
  top: -5px;
  left: 0;
}

.theme-switcher input[type="radio"]:checked + label, .theme-switcher input[type="radio"]:focus + label {
  transform: scaleY(1.8);
}

.logo {
  color: var(--color-white);
  cursor: pointer;
  fill: currentColor;
  transition: color var(--duration) var(--timing-function), fill var(--duration) var(--timing-function);
  padding-top: 20px;
  padding-bottom: 20px;
